import { createApp } from 'vue'; // Используем createApp для Vue 3
import App from './App.vue';
import router from './router';
import axios from 'axios';
import VueNumberInput from '@chenfengyuan/vue-number-input';
import axiosPlugin from './plugins/axios';

const app = createApp(App);

app.use(router); // Подключаем роутер
app.use(axiosPlugin); // Подключаем axios плагин
app.use(VueNumberInput); // Подключаем VueNumberInput

app.config.globalProperties.$BaseURL = "http://localhost:8080/";
app.config.globalProperties.$axios = axios;

app.mount('#app'); // Монтируем приложение в DOM
